.how-it-works {
  position: fixed;
  bottom: 12px;
  right: 15px;
  opacity: .5;
  -webkit-transition: .15s opacity linear;
  transition: .15s opacity linear; }
  .how-it-works:hover {
    opacity: .8;
    cursor: pointer; }
  .how-it-works_relative {
    position: absolute;
    right: 10px;
    bottom: 20px; }
  .how-it-works__modal .modal-title {
    font-size: 1.4rem; }
  .how-it-works__modal p {
    text-indent: 20px;
    -webkit-hyphens: auto;
        -ms-hyphens: auto;
            hyphens: auto; }
    .how-it-works__modal p::before {
      content: '\25CF';
      position: absolute;
      left: 0; }
  .how-it-works__modal p:not(:last-child) {
    margin-bottom: 15px; }
  @media screen and (max-width: 550px) {
    .how-it-works__modal .modal-title {
      font-size: 1.2rem; }
    .how-it-works__modal p {
      font-size: .9rem; }
    .how-it-works__modal p:not(:last-child) {
      margin-bottom: 10px; } }
