.alerts {
  position: fixed;
  right: 0;
  top: 65px;
  max-height: calc(100% - 65px);
  padding: 0 10px;
  padding-bottom: 15px;
  z-index: 3;
  overflow-x: auto; }
  .alerts .close {
    padding-top: .25rem;
    padding-right: .5rem; }
    .alerts .close:focus {
      outline: none; }
  .alerts .alert {
    padding: .5rem 1.25rem;
    padding-left: 1rem;
    max-width: 300px;
    margin-bottom: .75rem; }
    .alerts .alert-heading {
      font-size: 1rem;
      padding-right: .5rem;
      margin-bottom: .4rem; }
    .alerts .alert-description {
      font-size: .8rem; }
    .alerts .alert:last-child {
      margin-bottom: 0; }
